import React from "react";
import {
    Popover,
    Tooltip,
    Form,
    OverlayTrigger,
    Container,
    Accordion
} from "react-bootstrap";

import "./VisaForm.css"

const renderResearchPaperToolTip = (props) => (
    <Tooltip id="button-tooltip" {...props}>
        Author of research papers which appeared in an academic magazine registered in an academic research paper database used by Japanese national organizations.
    </Tooltip>
  );

  const showPopover = (title, content) => (
    <Popover id="popover-basic">
      <Popover.Header as="h3">{title}</Popover.Header>
      <Popover.Body>
        {content}
      </Popover.Body>
    </Popover>
  );

class VisaForm extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            visaTypes : ["Academic", "Technical", "Business"],
            degreeTypes: ['Doctors', 'Masters', 'Bachelors', 'Additional'],
            yearsWorkExperienceBrackets: [7, 5, 3],
            salaryBracketsMillion: [10, 9, 8, 7, 6, 5, 4, 3],
            score: 0,
            visaType: "academic"
        };

        this.handleCheckedChanged = this.handleCheckedChanged.bind(this);
        this.calculateVisaPoints = this.calculateVisaPoints.bind(this);
        this.handleValueChanged = this.handleValueChanged.bind(this);
        this.handleChangeVisaType = this.handleChangeVisaType.bind(this);
        this.drawVisaForm = this.drawVisaForm.bind(this);
    }

    handleChangeVisaType(event){
        this.setState({
            visaType: event.target.value,
            yearsWorkExperienceBrackets: event.target.value === "academic" ? [7, 5, 3] : [10, 7, 5, 3]
        })
    }

    handleValueChanged(event){
        this.setState({
            [event.target.name]: event.target.value
        }, this.calculateVisaPoints);
    }

    handleCheckedChanged(event){
        this.setState({
            [event.target.name]: event.target.checked
        }, this.calculateVisaPoints);
    }

    calculateVisaPoints() {
        let currentScore = 0;

        // Academic degree related points
        if ( this.state.hasDoctorsDegree ){
            currentScore += this.state.visaType === "business" ? 20 : 30;
        } else if ( this.state.hasMastersDegree ) {
            currentScore += 20;
        } else if ( this.state.hasBachelorsDegree ){
            currentScore += 10;
        }
        if ( this.state.hasAdditionalDegree){
            currentScore += 5;
        }
        console.log(this.state);

        // Work experience related points
        // Salary brackets change based on age
        let initialWorkExperiencePoints = 0;

        if (this.state.visaType === "academic"){
            initialWorkExperiencePoints = 15;
        } else if (this.state.visaType === "technical") {
            initialWorkExperiencePoints = 20;
        } else if (this.state.visaType === "business") {
            initialWorkExperiencePoints = 25;
        }


        let yearsWorkExperience = parseInt(this.state.yearsWorkExperience)
        for (let i = 0; i < this.state.yearsWorkExperienceBrackets.length; i++) {
            if ( yearsWorkExperience === this.state.yearsWorkExperienceBrackets[i]){
                currentScore += initialWorkExperiencePoints - 5*i;
                break;
            }
        }

        // minSalaryBracket doesn't apply to business visa
        let minSalaryBracketIndex = this.state.salaryBracketsMillion.length;

        // Age points don't apply to business visa
        if (this.state.visaType === "academic" || this.state.visaType === "technical"){
            
            let age = parseInt(this.state.age);

            // Age related points
            if (age < 29 ){
                currentScore += 15;
            } else if (age < 35 ){
                currentScore += 10;
                minSalaryBracketIndex -= 2;
            } else if (age < 40 ){
                currentScore += 5;
                minSalaryBracketIndex -= 3;
            }
            else {
                minSalaryBracketIndex -= 5;
            }
        }

        let salary = parseInt(this.state.salary);
        // Salary brackets change based on age
        for (let i = 0; i < this.state.salaryBracketsMillion.length; i++) {
            if ( salary >= this.state.salaryBracketsMillion[i] && minSalaryBracketIndex > i){
                currentScore += 40 - 5*i;
                break;
            }
        }

        if (this.state.visaType === "academic" || this.state.visaType === "technical"){
            let researchPoints = this.state.visaType === "academic" ? 20 : 15;

            let researchAchieved = 0;

            if (this.state.hasPatent){
                researchAchieved += 1;
            }

            if (this.state.receivedGovernmentGrants){
                researchAchieved += 1;
            }

            if (this.state.hasThreePapers){
                researchAchieved += 1;
            }
            
            if (this.state.hasEquivalentResearchRecord){
                researchAchieved += 1;
            }

            // Academic visas get extra points for having two
            if (this.state.visaType && researchAchieved >= 2){
                currentScore += researchPoints + 5; 
            } else if (researchAchieved >= 1){
                currentScore += researchPoints; 
            }
        }

        // This must go last since having too low a salary invalidates your application
        if (this.state.salary  === "0") {
            currentScore = 0;
        }

        this.setState({
            score: currentScore
        });
    }

    drawVisaForm() {
        return (
            <Container>
                    <Form>  
                        <Form.Group controlId="academic-background">
                            <Form.Label>Academic Background</Form.Label>
                            {this.state.degreeTypes.map((degreeType) => (
                                <div key={`degree-${degreeType}`} className="mb-3">
                                    <Form.Check 
                                        type="checkbox" 
                                        id={`degree-${degreeType}`}
                                        name={`has${degreeType}Degree`}
                                        label={`${degreeType} Degree`}
                                        onChange={this.handleCheckedChanged}
                                    />
                                </div>
                            ))}
                        </Form.Group>
                        <br />
                        <Form.Group controlId="professional-career">
                        <Form.Label>Professional Career</Form.Label>
                            {this.state.yearsWorkExperienceBrackets.map((years, index) => (
                                <div key={`yearsWorkExperience-${years}`} className="mb-3">
                                    <Form.Check 
                                        type="radio"
                                        id={`yearsWorkExperience-${years}`}
                                        name="yearsWorkExperience"
                                        value={years}
                                        label={index ? `${years}` : `>${years}`} 
                                        onChange={this.handleValueChanged}
                                    />
                                </div>
                            ))}
                            <div key={`yearsWorkExperience-below-minimum`} className="mb-3">
                                    <Form.Check 
                                        type="radio"
                                        id={`yearsWorkExperience-below-minimum`}
                                        name="yearsWorkExperience"
                                        value={0}
                                        label={`<${this.state.yearsWorkExperienceBrackets.slice(-1)[0]}`} 
                                        onChange={this.handleValueChanged}
                                    />
                                </div>
                        </Form.Group>
                        <br />
                        {
                        // Age is only valid for academic or technical visa
                        // This conditional rendering is kind of a hack
                        (this.state.visaType === "academic" || this.state.visaType === "technical") &&
                        <Form.Group controlId="age">
                            <Form.Label>Age</Form.Label>
                            <div className="mb-3">
                                <Form.Control
                                    required
                                    name="age"
                                    type="number"
                                    min="0"
                                    value={this.state.age}
                                    onChange={this.handleValueChanged}
                                />
                            </div>
                        </Form.Group>
                        }

                        
                        <Form.Group controlId="annual-salary">
                            <Form.Label>Annual Salary</Form.Label>
                            <div className="mb-3">
                                <Form.Select defaultValue={{ label: "Salary in million yen", value: 0 }} name="salary" value={this.state.salary} onChange={this.handleValueChanged}>
                                    {this.state.salaryBracketsMillion.map((salaryBracket) => (
                                        <option value={salaryBracket}>{`>${salaryBracket} million yen`}</option>
                                    ))}
                                    <option value={0}>{`<${this.state.salaryBracketsMillion.slice(-1)[0]} million yen`}</option>
                                </Form.Select>
                            </div>
                        </Form.Group>
                        <br />
                        <Form.Group controlId="bonus-points">
                            <Form.Label>Bonus Points</Form.Label>
                                {
                                // Research achievments are only valid for academic or technical visa
                                // This conditional rendering is kind of a hack
                                (this.state.visaType === "academic" || this.state.visaType === "technical") &&
                                <Form.Group controlId="bonus-points-academic">
                                    <Form.Label>Research Achievements</Form.Label>
                                    <Form.Check
                                        type="checkbox"
                                        className="mb-3"
                                        name="hasPatent"
                                        value={this.state.hasPatent}
                                        label="Hold a patent"
                                        onChange={this.handleCheckedChanged}
                                    />
                                    <Form.Check
                                        type="checkbox"
                                        className="mb-3"
                                        name="receivedGovernmentGrants"
                                        value={this.state.receivedGovernmentGrants}
                                        label="Received at least 3 public research grants"
                                        onChange={this.handleCheckedChanged}
                                    />
                                    <OverlayTrigger
                                        placement="right"
                                        overlay={renderResearchPaperToolTip}
                                    >
                                        <Form.Check
                                            type="checkbox"
                                            className="mb-3"
                                            name="hasThreePapers"
                                            value={this.state.hasThreePapers}
                                            label="Publish 3 papers in Japanese database"
                                            tool
                                            onChange={this.handleCheckedChanged}
                                        />
                                    </OverlayTrigger>
                                    <Form.Check
                                        type="checkbox"
                                        className="mb-3"
                                        name="hasEquivalentResearchRecord"
                                        value={this.state.hasEquivalentResearchRecord}
                                        label="Equivalent research record"
                                        onChange={this.handleCheckedChanged}
                                    />
                             </Form.Group>
                            }
                        </Form.Group>
                    </Form>
                </Container>
        )
    }

    render() {
        return (
            <body>
                <Container>
                    <p>
                        A tool for quickly calculating the points for  <OverlayTrigger
                                        placement="below"
                                        overlay={() => {showPopover("HSP", "Highly Skilled Professional")}}
                                    ></OverlayTrigger> visa.
                    </p>
                    <Accordion>
                        <Accordion.Item eventKey="0">
                            <Accordion.Header>Disclaimer</Accordion.Header>
                            <Accordion.Body>
                                Disclaimer of Liability. EXCEPT AS EXPRESSLY SET FORTH IN THIS AGREEMENT, NEITHER RECIPIENT NOR ANY CONTRIBUTORS SHALL HAVE ANY LIABILITY FOR ANY DIRECT, INDIRECT, INCIDENTAL, SPECIAL, EXEMPLARY, OR CONSEQUENTIAL DAMAGES (INCLUDING WITHOUT LIMITATION LOST PROFITS), HOWEVER CAUSED AND ON ANY THEORY OF LIABILITY, WHETHER IN CONTRACT, STRICT LIABILITY, OR TORT (INCLUDING NEGLIGENCE OR OTHERWISE) ARISING IN ANY WAY OUT OF THE USE OR DISTRIBUTION OF THE PROGRAM OR THE EXERCISE OF ANY RIGHTS GRANTED HEREUNDER, EVEN IF ADVISED OF THE POSSIBILITY OF SUCH DAMAGES.
                            </Accordion.Body>
                        </Accordion.Item>
                    </Accordion>
                    <Form.Group>
                        <Form.Label>Visa Type</Form.Label>
                        {this.state.visaTypes.map((visaType, index) => (
                            <Form.Check
                                type="radio"
                                inline
                                defaultChecked={index === 0 ? true: false}
                                className="mb-3"
                                value={visaType.toLowerCase()}
                                label={visaType}
                                name="visaType"
                                onChange={this.handleChangeVisaType}
                                className="mb-3"
                            />
                        ))}
                    </Form.Group>
                    {this.drawVisaForm()}
                </Container>
                
                <div>
                    <h2 className="mb-3">{`Score: ${this.state.score}`}</h2>
                </div>
            </body>
        )
    }
}

export default VisaForm;